import React from 'react'

const CTA = (props) => {
    const { title, subtitle, CTA } = props

    return (
        <div class="bg-yellow-nano">
            <div class="max-w-screen-xl mx-auto text-center py-12 px-4 sm:px-6 lg:py-16 lg:px-8">
                <h3 class="pb-8 text-3xl leading-9 font-extrabold tracking-tight text-gray-900 sm:text-4xl sm:leading-10">
                    {title}<br />
                </h3>
                <h4 class=" text-xl leading-9 font-semibold text-gray-900 tracking-tight  sm:text-2xl sm:leading-10">{subtitle}</h4>
                <div class="mt-8 flex justify-center">
                    <div class="bg-white inline-flex rounded-md shadow">
                        <a href="#" class="inline-flex items-center justify-center px-5 py-3 border border-transparent text-base leading-6 font-medium rounded-md text-yellow-nano bg-white focus:outline-none focus:shadow-outline transition duration-150 ease-in-out">
                            {CTA}
                        </a>
                    </div>

                </div>
            </div>
        </div>

    )
}
export default CTA